<template>
  <div :class="$style.container">
    <cds-texting format="h1">Ya estás pronto a comenzar!</cds-texting>
    <cds-texting format="h5">Comenzarás tu vida con un ahorro para tu pensión de $1.000.000</cds-texting>
    <img
      :class="$style.image"
      src="@/assets/hipster.png">
    <cds-button
      @click="nextStep"
      :text="'Continuar'"/>
  </div>
</template>

<script>
// @ is an alias to /src
import { mapActions } from 'vuex';

const textGetExampleSuccess = 'getExampleSuccess';
const textGetExampleUnauthorized = 'getExampleUnauthorized';

export default {
  name: 'Ready',
  data() {
    return {
    };
  },
  methods: {
    nextStep() {
      this.$router.push({ path: '/juego' });
    },
    created() {
      this.getExampleSuccess();
      this.getExampleUnauthorized();
    },
    ...mapActions({
      getExampleSuccess: textGetExampleSuccess,
      getExampleUnauthorized: textGetExampleUnauthorized,
    }),
  },
  created() {
    this.$featureToggle.merge({ ADDED_FEATURE: true });
  },
};
</script>

