import axios from 'axios';

const httpClientSuccess = axios.create({
  baseURL: 'https://www.cuprum.cl/ApiSitioPublico/SeriesValorCuota/ValorCuota', // valores cuota
  timeout: 60000,
  headers: {},
});

const httpClientUnauthorized = axios.create({
  baseURL: 'https://fa-hackaton.firebaseio.com/recomendacion.json', // recomendaciones
  timeout: 60000,
  headers: {},
});

export const getExampleSuccess = async () => httpClientSuccess.get();
export const getExampleUnauthorized = async () => httpClientUnauthorized.get();
