import Vue from 'vue';
import Vuex from 'vuex';
import async from './modules/async';

Vue.use(Vuex);

export const actions = {};

export const getters = {
  recomendacionesFyF: (state) => {
    return state.async.getExampleUnauthorized.data.data;
  },
  valoresCuota: (state) => {
    return state.async.getExampleSuccess.data.data;
  },
};

export const mutations = {};

export default new Vuex.Store({
  modules: {
    async,
  },
  actions,
  getters,
  mutations,
  strict: process.env.NODE_ENV !== 'production',
});
